.header {
  background: var(--background);
}
.container_header  {
  display: flex;
}
.header_wrapper {
  display: flex;
  align-items: center;
  composes: pb-4 pt-4 from global;
  justify-content: space-between;
}

.link {
  border: none;
  background-color: transparent;
  display: flex;
  align-items: center;
  font-family: 'JetBrainsMono', Arial, sans-serif;
  composes: text text_type_main-medium from global;
}
.header_link_order {
  composes: pr-5 pb-5 pt-5 text_color_inactive from global;
  composes: link;
  text-decoration: none;
  color: #8585ad;
  text-decoration: none;
}

.header_link_order svg {
  padding-right: 8px;
}
.header_link_account {
  composes: link;
  composes: pb-4 pt-4 text_color_inactive from global;
  padding-right: 20px;
  padding-left: 23px;
  text-decoration: none;
}
.header_link_account svg {
  margin-right: 11px;
 
}
 
.active {
  text-decoration: none;
  color: #ffffff;
  composes: pr-5 pb-5 pt-5 text_color_inactive from global;
  composes: link;
  
}
.active svg {
  margin-right: 11px;
}
.decoration {
  text-decoration:  none;
}